.contact-us-wrap
.contact-us {
  top: 0;
  margin-right: 15px;
  display: inline-block !important;
  background-color: transparent;
  font-weight: bold; }

.contact-us i.fa,
.contact-us i {
  margin-right: 5px;
  margin-left: 0; }

.contact-us i:not(.fa) {
  display: none; }

.contact-us:hover i {
  display: inline-block !important; }

.contact-us,
.contact-us:hover i.fa {
  color: lightskyblue; }

.contact-us i.fa,
.contact-us:hover i {
  color: red; }

.contact-us:hover i.fa {
  font-size: 110%; }
