#page-wrap {
  width: 1000px;
  margin: 25px auto; }

.drop-down-menu a,
a {
  text-decoration: none;
  text-decoration-style: none; }

ul,
.ul {
  list-style: none; }

.drop-down-menu,
.drop-down-menu .ul,
.drop-down-menu .li,
.drop-down-menu a {
  margin: 0;
  padding: 0;
  width: 100%;
  max-width: 100%;
  text-align: center; }

/* 
	LEVEL ONE
*/
.drop-down-menu {
  /* position: relative;    */
  width: 100%;
  cursor: pointer; }
  .drop-down-menu .li {
    font-weight: bold;
    font-size: 16px;
    float: left;
    position: relative;
    height: 60px; }
    .drop-down-menu .li a {
      display: block;
      padding: 20px 8px;
      color: white;
      position: relative;
      z-index: 1032; }
  .drop-down-menu a:hover {
    font-size: 110%;
    color: greenyellow;
    position: relative; }
  .drop-down-menu:hover li a {
    background: rgba(0, 0, 0, 0.7); }

/* 
    LEVEL TWO
*/
.drop-down-menu .ul {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1031; }
  .drop-down-menu .ul .li {
    font-weight: normal;
    color: #000;
    /* border-bottom: 1px solid #ccc;  */
    margin-top: 2px; }

.drop-down-menu .ul .li button,
.drop-down-menu .li button,
.drop-down-menu .ul a button,
.drop-down-menu a button,
.drop-down-menu .li a,
.drop-down-menu .ul .li a {
  display: block;
  width: 100%;
  height: 100%;
  border: none;
  /* background: #eee !important;  */ }

.drop-down-menu .li button,
.drop-down-menu .ul .li button,
.drop-down-menu .ul a button,
.drop-down-menu a button {
  background: transparent; }

.drop-down-menu .li a:hover,
.drop-down-menu .ul .li a:hover {
  display: block;
  color: greenyellow;
  /* background: #F3D673 !important;  */ }

.drop-down-menu .li {
  background-image: -moz-linear-gradient(top, rgba(50, 60, 80, 0.8), rgba(50, 60, 80, 0));
  background-image: -o-linear-gradient(top, rgba(50, 60, 80, 0.8), rgba(50, 60, 80, 0));
  background-image: -webkit-linear-gradient(top, rgba(50, 60, 80, 0.8), rgba(50, 60, 80, 0));
  background-image: linear-gradient(to bottom, rgba(50, 60, 80, 0.8), rgba(50, 60, 80, 0)); }

.drop-down-menu .ul .li a,
.drop-down-menu .ul .li {
  background-image: -moz-linear-gradient(top, #0a325a, rgba(50, 60, 80, 0.5));
  background-image: -o-linear-gradient(top, #0a325a, rgba(50, 60, 80, 0.5));
  background-image: -webkit-linear-gradient(top, #0a325a, rgba(50, 60, 80, 0.5));
  background-image: linear-gradient(to bottom, #0a325a, rgba(50, 60, 80, 0.5)); }

.drop-down-menu .li a.hover,
.drop-down-menu .li a:hover,
.drop-down-menu .ul .li a:hover {
  border-color: #66afe9;
  outline: 0;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6); }

/**.......................................................................................................
* Scroll Up and Down of a section
.......................................................................................................*/
.scroll-section, .scroll-section .scroll-body {
  width: inherit;
  /* height: 300px; */
  cursor: pointer;
  position: relative;
  /* important to positioned the absolute elements within container */ }
  .scroll-section .scroll-body {
    width: 100%;
    height: 100%;
    clear: both;
    overflow: hidden; }

.scroll-section, .scroll-section .scroll-body {
  padding: 0;
  margin: 0; }
  .scroll-section .scroll-body .summary-pane {
    position: absolute;
    height: 100%;
    max-height: 100%;
    width: 20px;
    max-width: 20px;
    padding: 10px 1px;
    overflow-y: auto;
    overflow-x: hidden;
    word-break: normal;
    color: greenyellow; }
    .scroll-section .scroll-body .summary-pane.left {
      left: 0; }
    .scroll-section .scroll-body .summary-pane.right {
      right: 0; }
    .scroll-section .scroll-body .summary-pane * {
      cursor: not-allowed;
      word-break: normal; }
  .scroll-section .scroll-content {
    padding: 10px 0;
    height: auto;
    position: relative;
    display: block; }
    .scroll-section .scroll-content .li {
      float: none;
      /* float: left prevents calculative height of .li, and returns height always 0 */ }
  .scroll-section:hover .controls, .scroll-section .scroll-body:hover .controls {
    opacity: 1; }

/**.......................................................................................................
* End Scroll Up and Down of a section
.......................................................................................................*/
